// Lib imports
// =====================================================
@import "base/core";

// Modules imports
// =====================================================
@import "modules/header";
@import "modules/container";
@import "modules/main";

// Content styles
// =====================================================
html {
  font-size: 10px;
}

body {
  background: $softGrey;
  font-family: $UbuntuFontFamily;
  font-size: $baseFont;

  @include maxScreen($middle) {
    font-size: $mobileFont;
  }

  @include maxScreen($small) {
    font-size: $mobileFont;
  }
}